.wpsm_box.leftfloat_box{float:left}.wpsm_box.rightfloat_box{float:right}.wpsm_box.nonefloat_box{float:none}.wpsm_box.gray_type{color:#666;background:#f9f9f9}.wpsm_box.red_type{color:#de5959;background:#ffe9e9}.wpsm_box.green_type{color:#5f9025;background:#ebf6e0}.wpsm_box.blue_type{color:#5091b2;background:#e9f7fe}.wpsm_box.yellow_type{color:#c4690e;background:#fffdf3}.wpsm_box.dashed_border_type{border:1px dashed #ccc}.wpsm_box.solid_border_type{border:1px solid #ccc}.wpsm_box.transparent_type{background-color:transparent}.wpsm_box{color:#363636;min-height:52px;padding:18px 26px;margin:0 0 31px 0;overflow:auto;font-size:17px}.wpsm_box p:last-child{margin:0}.wpsm_box.warning_type{background-color:#fff7f4;color:#a61818}.wpsm_box.standart_type{background-color:#f9f9f9}.wpsm_box.info_type{background-color:#f0ffde}.wpsm_box.error_type{background-color:#ffd3d3;color:#dc0000}.wpsm_box.download_type{background-color:#e8f9ff}.wpsm_box.note_type{background-color:#fffce5}.wpsm_box.download_type i,.wpsm_box.error_type i,.wpsm_box.info_type i,.wpsm_box.note_type i,.wpsm_box.standart_type i,.wpsm_box.warning_type i{font-family:Font Awesome\ 5 Pro;font-weight:400;font-style:normal;text-decoration:inherit;-webkit-font-smoothing:antialiased;display:inline;width:auto;height:auto;line-height:normal;vertical-align:baseline;background-image:none;background-position:0 0;background-repeat:repeat;margin-top:0;font-size:27px;float:left;margin:0 14px 10px 0}.wpsm_box.warning_type i:before{content:"\f071";color:#e25b32;font-weight:900}.wpsm_box.info_type i:before{content:"\f0c6";color:#53a34c;font-weight:300}.wpsm_box.error_type i:before{content:"\f05e";color:#dc0000;font-weight:300}.wpsm_box.download_type i:before{content:"\f019";color:#1aa1d6;font-weight:900}.wpsm_box.note_type i:before{content:"\f044";color:#555;font-weight:300}.wpsm_box.leftfloat_box{margin:0 31px 31px 0}.wpsm_box.rightfloat_box{margin:0 0 31px 31px}
.wpsm-titlebox{margin:0 0 31px;padding:18px 26px;position:relative}.wpsm-titlebox>strong:first-child{background:none repeat scroll 0 0 #fff;float:left;font-size:17px;font-weight:600;left:11px;line-height:18px;margin:0 0 -9px!important;padding:0 10px;position:absolute;text-transform:uppercase;top:-10px}.wpsm-titlebox.wpsm_style_1{border:3px solid #e7e4df}.wpsm-titlebox.wpsm_style_1>strong:first-child{color:silver}.wpsm-titlebox.wpsm_style_2{border:3px solid #333}.wpsm-titlebox.wpsm_style_2>strong:first-child{color:#333}.wpsm-titlebox.wpsm_style_3{border:3px solid #fb7203}.wpsm-titlebox.wpsm_style_3>strong:first-child{color:#fb7203}.wpsm-titlebox.wpsm_style_4{border:3px double #e7e4df}.wpsm-titlebox.wpsm_style_4>strong:first-child{color:#666}.wpsm-titlebox.wpsm_style_5{border:3px solid #61baea}.wpsm-titlebox.wpsm_style_5>strong:first-child{color:#333}.wpsm-titlebox.wpsm_style_main,.wpsm-titlebox.wpsm_style_secondary{border-width:3px;border-style:solid}.wpsm_sep.vc_separator h4{font-size:21px}.wpsm_sep.wpsm_uppercase{text-transform:uppercase}.wpsm-titlebox p:last-child{margin:0}
